@import '../../styles/global';
@import '../../styles/var';

.header {
    min-height: 120px;
    background-color: $base-color;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;

    > button {
        display: flex;
        align-items: center;
        position: relative;
        cursor: pointer;
        border: none;
        background-color: black;
        font-size: 2.2rem;
        color: white;
        font-weight: 700;
        padding-left: 3rem;
        > img {
            margin: 1rem;
        }
    }
    &_dropdown ul {
        position: absolute;
        background-color: black;
        opacity: 0.8;
        padding: 1rem 0;
        z-index: 10;
    }
    &_dropdown li {
        color: white;
        padding: 1rem 3rem;
        font-size: 1.5rem;
    }
}

@media screen and (min-device-width: 250px) and (max-device-width: 767px) {
    .header {
        min-height: 60px;
    }

    .container {
        img {
            height: 30px;
            width: 30px;
        }
        > button {
            font-size: 1.2rem;
            padding-left: 1rem;
            > img {
                display: inline-block;
                margin: 0.5rem;
            }
        }
        &_dropdown ul {
            right: 0;
        }

        &_dropdown li {
            padding: 1rem 1rem;
            font-size: 1.2rem;
        }
    }

    .logo_img {
        margin-left: 5px;
        height: 60px;
        width: 60px;
    }
}
